import { Injectable } from "@angular/core";
// import { HttpClient } from "selenium-webdriver/http";
import { HttpClient, HttpErrorResponse } from "@angular/common/http";
import { Observable } from 'rxjs/Observable';
import { Book } from "../model/book.model";
import { map } from 'rxjs/operators';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/map';
import { environment } from "../../../environments/environment";
import 'rxjs/add/observable/of';
import { of } from 'rxjs';


@Injectable()
export class BooksService {
    baseUrl: string = environment.apiUrl;
    errorMessage: string;
    apiEndPointUrl: string;
    books: Book[];
    locallUrl = './api/db.json';

    constructor(private http: HttpClient) {
    }

    loadBooks() {
        this.http.get(this.locallUrl)
            .subscribe(data => {
                this.books = <Book[]>data;
                console.log(this.books[0].id);
            })
    }


    getAllBooks(): Observable<any> {
        return Observable.of(this.books);
    }


    updateBook(book: Book): Observable<Book> {
        return new Observable((observer) => {
            let bookIndex = this.books.indexOf(this.books.find(b => b.id === book.id));
            this.books[bookIndex] = book;
            observer.next(book);
            observer.complete();
        
        })
    }

    addBook(book: any): Observable<void> {
        return new Observable((observer) => {
            this.books.push(book);
            observer.next();
            observer.complete();
        })
    }

    deleteBook(book: Book): Observable<void> {
        return new Observable((observer) => {
            let bookIndex = this.books.indexOf(this.books.find(b => b.id == book.id));
            this.books.splice(bookIndex, 1);
            observer.next();
            observer.complete();
        })
    }


}